import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, ERROR, findByExample, getSelectedItem, InputGroupInline, InputType, showToast, WindowComponent } from 'summer';
import { CLASS_NAME, LimiteImpostos } from '../../../../models/dtos/mlf/limiteImpostos';

interface ConfigLimiteMEEPPPEditViewProps extends EditViewProps<LimiteImpostos> {}

const defaultWidth: number = 110;

const ConfigLimiteMEEPPPEditView: FC<ConfigLimiteMEEPPPEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const { reset, ...formProps } = useForm<LimiteImpostos>({ defaultValues: selectedItem, mode: 'onBlur' });

  useEffect(
    () => {
      const LimiteImpostosExample: LimiteImpostos = {};

      findByExample(LimiteImpostosExample, CLASS_NAME, {
        errorFunction: mensagem => {
          showToast(mensagem, ERROR);
        },
        thenFunction: result => {
          reset(result);
        },
      });
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );
  return (
    <WindowComponent title="Manutenção Limites Impostos (ME/EPP/Presumido)" wikiId={3462}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              id="limiteMe"
              label="Limite ME"
              labelWidth={defaultWidth}
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              placeholder="Informe o Limite ME"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="limiteEpp"
              label="Limite EPP"
              labelWidth={defaultWidth}
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o Limite EPP"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="limiteLucroPresumido"
              label="Limite Lucro Presumido"
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              placeholder="Informe o Limite Lucro Presumido"
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default ConfigLimiteMEEPPPEditView;
